/** @jsx jsx */
import { css, jsx } from "@emotion/react"
import { Avatar } from "../components/Avatar"
import { Header } from "../components/Header"
import type { HeadFC, PageProps } from "gatsby"
import { pageStyles, paragraphStyles } from "../common/styles"
import { SocialLinks } from "../components/SocialLinks"
import { setTheme } from "../common/theme"
import Family from '../images/family.jpeg'
import Farm from '../images/farm.jpeg'
import Milwaukee from '../images/milwaukee.jpeg'
import { ReactElement, ReactFragment } from "react"

const footerStyle = css`
    margin-top: 50px;
`

const avatarStyle = css`
    width: 100%;
    height: auto;
    border-radius: 10px;
`

const sectionWrapper = css`
    display:flex;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 120px;
`

const imageWrapperStyle = css`
    width: 45%;
    min-width: 200px;
`

interface sectionProps {
    left: ReactElement
    right: ReactElement
}

const Section  = function({left, right}: sectionProps) {
    return <div css={sectionWrapper}>
        <div css={{width: '45%', minWidth:'200px'}}>{left}</div>
        <div css={{width: '45%'}}>{right}</div>
    </div>
}

const AboutMePage: React.FC<PageProps> = () => {
    setTheme()
    return (
      <main css={pageStyles}>
        <Header/>
        <Section 
            left={<img src={Farm} css={avatarStyle} alt="Picture of cows"/>} 
            right={ <p css={paragraphStyles}>
                    My name is Joe.  I grew up working on my fathers farm which instilled in me the work ethic that I possess today.
                </p>
            }
        />
        <Section 
            left={<p css={paragraphStyles}>
                    I went to UW Milwaukee and received my bachelors degree in computer science.
            </p>} 
            right={<img src={Milwaukee} css={avatarStyle} alt="Milwaukee"/>}
        />
        <Section 
            left={<img src={Family} css={avatarStyle} alt="Picture of my family"/>} 
            right={ <p css={paragraphStyles}>
                    Today I work as a senior software engineer at Widen an aquia company specializing in front end.  
                    I have a wonderful wife and 2 boys that I enjoy spending as much time with as possible.
                </p>
            }
        />
        <div css={footerStyle}>
            <SocialLinks/>
        </div>
      </main>
    )
  }
  
  export default AboutMePage